import React, { useState, useEffect } from 'react';
import Layout from './layout'
import { Typography, Box, Avatar, Card, CardContent, Link } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import api from '../../api';

const useStyles = makeStyles(() => ({
  listBox: {
    marginTop: 25
  },
  w100: {
    width: '100%',
  },
  avatar: {
    color: 'white',
    backgroundColor: 'orangered'
  },
  card: {
    marginTop: 15
  },
  cardContent: {
    display: 'flex',
    gap: 15
  },
  cardBody: {
    flexGrow: 1,
    width: 1
  },
  photo: {
    width: 300,
    height: 300,
    display: 'flex',
    justifyContent: 'center',
    overflow: 'hidden'
  },
  cardNamePrice: {
    display: 'flex',
    justifyContent: 'space-between'
  },
  description: {
    marginTop: 10
  },
  photos: {
    marginTop: 10,
    display: 'flex',
    flexWrap: 'wrap',
    gap: 10
  },
  additionalPhoto: {
    width: 80,
    height: 100,
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    border: '1px solid orangered',
    overflow: 'hidden'
  },
  info: {
    marginTop: 10,
    display: 'flex',
    justifyContent: 'flex-end'
  }
}));

const Carts = () => {
  const [ carts, setCarts ] = useState([]);
  const classes = useStyles();
  useEffect(() => {
    fetchCarts();
  }, [])

  const fetchCarts = () => {
    api.getCarts().then((data) => {
      console.log(data)
      if (data.error) {
        // setError(data.error);
        console.log(data.error);
      } else {
        setCarts(data.carts);
      }
    });
  }

  return (
    <Layout>
      <Box marginY={1}>
        <Typography variant='h4'>All Carts</Typography>
      </Box>
      <Box className={classes.listBox}>
        {carts.map((cart, idx) => (
          <Card className={classes.card} key={idx}>
            <CardContent className={classes.cardContent}>
              <Box className={classes.photo}>
                <img className={classes.w100} src={cart.photo} alt="logo"/>
              </Box>
              <Box className={classes.cardBody}>
                <Box className={classes.cardNamePrice}>
                  <Typography variant="h5" component="h2">{cart.name}</Typography>
                  <Typography variant="h4" component="h2">${cart.price}</Typography>
                </Box>
                <Box className={classes.description}>
                  <Typography>{cart.description}</Typography>
                </Box>
                <Box className={classes.photos}>
                  {cart.photos.map((photo, idx) => (
                    <Box className={classes.additionalPhoto} key={idx}>
                      <img className={classes.w100} src={photo} alt="photo"/>
                    </Box>
                  ))}
                </Box>
                <Box marginTop={1}>
                  <Link href={cart.url} target="_blank">{cart.url}</Link>
                </Box>
                <Box className={classes.info}>
                  <Typography>{cart.user && cart.user.email}</Typography>
                </Box>
              </Box>
            </CardContent>
          </Card>
        ))}
      </Box>
    </Layout>
  )
}

export default Carts;