import React, { useState, useEffect } from 'react';
import { useSelector } from "react-redux"
import copy from 'copy-to-clipboard';
import { Snackbar, Button } from '@material-ui/core';
import { FileCopyOutlined, Add } from '@material-ui/icons';
import Card from '../components/card';
import NoCard from '../components/nocard';
import Layout from './layout';
import api from '../api';

const Main = (props) => {
  const [error, setError] = useState(false);
  const [limit] = useState(12);
  const [skip, setSkip] = useState(0);
  const [size, setSize] = useState(0);
  const [cards, setCards] = useState([]);
  const [sharedId, setSharedId] = useState('');
  const [sharedEmail, setSharedEmail] = useState('');
  const [loading, setLoading] = useState(false);

  const { email, _id } = useSelector(state => state.auth);

  useEffect(() => {
    const userid = props.match.params.userid;
    setSharedId(userid);
  }, [props]);

  useEffect(() => {
    if (!sharedId) return
    loadCards();
  }, [sharedId]);

  const loadCards = () => {
    setLoading(true);
    api.getProducts(skip, limit, 1, sharedId).then((data) => {
      setLoading(false);
      if (data.error) {
        setError(data.error);
      } else {
        setCards(data.data);
        setSharedEmail(data.email);
        setSize(data.size);
        setSkip(0);
      }
    });
  };

  const loadMore = () => {
    let toSkip = skip + limit;
    api.getProducts(toSkip, limit, 1, sharedId).then((data) => {
      if (data.error) {
        setError(data.error);
      } else {
        setCards([...cards, ...data.data]);
        setSize(data.size);
        setSkip(toSkip);
      }
    });
  };

  const loadMoreButton = () => {
    return (
      size > 0 &&
      size >= limit && (
        <Button onClick={loadMore} variant='contained' className='more-btn'>
          more <Add />
        </Button>
      )
    );
  };

  const copyShareUrl = () => {
    copy(`http://www.ollacart.com/share/${_id}`);
  }

  return (
    <Layout sharedEmail={sharedEmail}>
      {sharedEmail === email && email && <div className='shared-header'>
        <div className='shared-url'>ollacart.com/share/{_id}<FileCopyOutlined onClick={copyShareUrl}/></div>
      </div>}
      <div>
        <div className='d-flex flex-wrap justify-content-center'>
          {cards.map((card, i) => (
            <div key={i} className='shop-item'>
              <Card card={card}/>
            </div>
          ))}
        </div>
        {loadMoreButton()}
      </div>
      {!cards.length &&<NoCard loading={loading}/>}
      <Snackbar open={!!error} autoHideDuration={6000} anchorOrigin={{vertical: 'top', horizontal: 'center'}} onClose={() => setError('')}>
        <div className="error-msg">{error}</div>
      </Snackbar>
    </Layout>
  );
};

export default Main;
