import React, { useState, useEffect } from 'react';
import { AppBar, Typography, Button, Toolbar, Box, Avatar } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { useHistory } from 'react-router-dom'
import { useSelector } from "react-redux"
import Logo from '../../assets/img/Olla - g48.png';

const useStyles = makeStyles(() => ({
  avatar: {
    color: 'white',
    backgroundColor: 'orangered',
    width: '34px',
    height: '34px'
  },
  logoText: {
    flexGrow: 1,
    marginLeft: 10
  },
  adminContainer: {
    display: 'flex',
    flexDirection: 'column',
    minHeight: '100vh',
    backgroundColor: '#f5f5f5'
  },
  container: {
    flexGrow: 1,
    width: '1200px',
    maxWidth: '100%',
    padding: '20px',
    margin: 'auto'
  }
}));

const Layout = ({ children }) => {
  const history = useHistory();
  const { email } = useSelector(state => state.auth)

  const classes = useStyles();

  const goTo = (url) => {
    history.push(url);
  }
  return (
    <Box className={classes.adminContainer}>
      <AppBar position="static">
        <Toolbar>
          <Box>
            <img src={Logo} />
          </Box>
          <Typography variant="h6" className={classes.logoText}>
            OllaCart
          </Typography>
          <Button color="inherit" onClick={() => goTo('/admin/users')}>Users</Button>
          <Button color="inherit" onClick={() => goTo('/admin/carts')}>Carts</Button>
          <Avatar className={classes.avatar}>{email.toUpperCase()[0]}</Avatar>
        </Toolbar>
      </AppBar>
      <Box className={classes.container}>
        {children}
      </Box>
    </Box>
  )
}

export default Layout;