import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from "react-redux"
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";

import { Provider } from "react-redux";
import { store } from "Redux@Helpers";
import Profile from './views/profile';
import Signin from './views/signin';
import Home from './views/home';
import Share from './views/share';
import Privacy from './views/privacy';
import Support from './views/support';
import Terms from './views/terms';
import Personal from './views/personal';
import ManageUsers from './views/admin/users';
import ManageCarts from './views/admin/carts';
import { actions } from './redux/_actions';

function AppContainer() {
  const dispatch = useDispatch();
  const { email, role } = useSelector(state => state.auth)
  console.log(email, role)

  useEffect(() => {
    const token = localStorage.getItem('token');
    if (token && !email) {
      dispatch(actions.verify(token));
    }
  }, [email, dispatch])

  return (
    <div>
      <Router>
        <Switch>
          <Route path="/" name='home' exact component={Home} />
          <Route path="/signin" name='signin' exact component={Signin} />
          <Route path="/profile" name='profile' exact component={Profile} />
          <Route path="/share/:userid" name='share' exact component={Share} />
          <Route path="/privacy-policy" exact component={Privacy}/>
          <Route path="/terms-of-service" exact component={Terms}/>
          <Route path="/personal-data" exact component={Personal}/>
          <Route path="/support" exact component={Support}/>
          {(role === 'admin') && (
            <>
              <Route path="/admin/users" exact component={ManageUsers}/>
              <Route path="/admin/carts" exact component={ManageCarts}/>
            </>
          )}
        </Switch>
      </Router>
    </div>
  );
}

export default function App() {
  return (
    <Provider store={store}>
      <AppContainer />
    </Provider>
  );
}