import React, { useState, useEffect } from 'react';
import { Person, PersonAdd, CloudUpload, Home } from '@material-ui/icons';
import { useHistory, useLocation } from 'react-router-dom'
import { useSelector } from "react-redux"

const Layout = ({ children, sharedEmail = '' }) => {
  // const [groupMode, setGrupMode] = React.useState(0)
  const { email, _id } = useSelector(state => state.auth);
  const history = useHistory();
  const location = useLocation();

  const [page, setPage] = useState('home');

  useEffect(() => {
    if (location.pathname.indexOf('/share') === 0) setPage('share');
    else if (location.pathname.indexOf('/privacy-policy') === 0) setPage('privacy');
    else if (location.pathname.indexOf('/terms-of-service') === 0) setPage('terms');
    else if (location.pathname.indexOf('/personal-data') === 0) setPage('personal');
    else if (location.pathname.indexOf('/support') === 0) setPage('support');
    else if (location.pathname.indexOf('/profile') === 0) {
      if (!email) goTo('/signin');
      setPage('profile');
    }
    else if (location.pathname.indexOf('/signin') === 0) {
      if (email) goTo('/');
      setPage('signin');
    }
    else {
      setPage('home');
      if (!email) goTo('/signin');
    }
  }, [email, location]);

  const goTo = (url) => {
    history.push(url);
  }

  return (
    <div className='main-content'>
      <div className='menu'>
        <div className='d-flex justify-content-between'>
          {/* <div className='left-menu'>
            <div className='left-menu-content'>
              <div className={ groupMode ? '' : 'selected' } onClick={() => setGrupMode(0)}>
                <Group />
              </div>
              <div className={ groupMode ? 'selected' : '' } onClick={() => setGrupMode(1)}>
                <Person />
              </div>
            </div>
          </div> */}
          <div className='right-menu'>
            <div className='menu-item'>
              {page !== 'share' && email && <CloudUpload onClick={() => goTo(`/share/${_id}`)}/>}
              {page === 'share' && email && <Home onClick={() => goTo('/')}/>}
            </div>
          </div>
          {page === 'share' && sharedEmail && <div className='share-email'>{sharedEmail}</div>}
          <div className='right-menu'>
            <div className='menu-item'>
              {page !== 'profile' && email && <Person onClick={() => goTo('/profile')}/>}
              {page === 'profile' && email && <Home onClick={() => goTo('/')}/>}
              {!email && <PersonAdd onClick={() => goTo('/signin')}/>}
            </div>
          </div>
        </div>
      </div>
      <div>{children}</div>
    </div>
  )
}

export default Layout;
